*{
  box-sizing: border-box;
}

@font-face {
  font-family: 'S-CoreDream-3Light';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_six@1.2/S-CoreDream-3Light.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Giants-Bold';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2307-1@1.1/Giants-Bold.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}

:root {
  --gray100: #F2F2F2;
  --gray200: #D9D9D9;
  --gray300: #808080;
  --gray400: #333333;
  --gray500: #262626;
  --gray600: #1A1A1A;
  --gray700: #0D0D0D;
  --purple: #8284FA;
  --purpledark: #5E60CE;
  --blue: #4EA8DE;
  --bluedark: #1E6F9F;
  --danger: #E25858;

  --mw : 800px;
}

a {
  text-decoration: none;
  color: inherit;
}

body {
  background-color: var(--gray600);
  color: var(--gray100);
  font-family: 'S-CoreDream-3Light';
}

header {
  height: 200px;
  background-color: var(--gray700);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
}

header h1{
  font-size: 2rem;
  font-family: 'Giants-Bold';
}

header h1::before{
  content: "\f135";
  font-family: "Font Awesome 6 Free";
  font-weight: 900;
  /* font-weight 값
  Solid → 900
  Regular, Brands → 400
  Light → 300 */
  color: var(--purpledark);
  display: inline-block;
  transform: rotate(315deg) translate(-4px, -4px);
}


header span:first-of-type{
  color: var(--blue);
}
header span:last-of-type{
  color: var(--bluedark);
}



/*  */
main {
  max-width: var(--mw);
  width: calc(100% - 4rem);
  margin: auto;
}

.text_input {
  display: flex;
  gap: 1rem;
  position: relative;
  top: -2rem;
}

.text_input input,
.text_input button{
  border-radius: 0.5rem;
  padding: 1rem 2rem;
  border: none;
}   
.text_input input{
  background-color: var(--gray500);
  color: var(--gray100);
  flex-grow: 1;
}
.text_input input:focus{
  background-color: var(--gray400);
}


.text_input button{
  background-color: var(--blue);
  color: var(--gray100);
}
.text_input button i {
  margin-left: 0.5rem;
}

.text_input button:hover,
.text_input button:focus{
  background-color: var(--bluedark);
}

@media (max-width:500px){
  .text_input{
      flex-direction: column;
  }
}



.total {
  text-align: right;
  font-size: 0.75rem;
  color: var(--purpledark);
  margin-bottom: 1rem;
}
.total span{
  display: inline-block;
  background-color: var(--gray300);
  color: var(--gray100);
  padding: 0.2em 0.4em;
  border-radius: 50%;
}


.list li {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  background-color: var(--gray500);
  margin-bottom: 1rem;
  border-radius: 0.5rem;
}

.list li > p {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 1rem 2rem;
}
.list li i {
  display: inline-block;
  padding: 1rem;
  transition: 0.5s;
}

.list li i:hover{
  color: var(--danger);
}

.popup {
  /* background-color: var(--gray100); */
  background-color: rgba(255,255,255,0.7);
  backdrop-filter: blur(5px);
  color: var(--gray700);
  padding: 3rem;
  /* position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0; */
}

.nolist {
  text-align: center;
}
.emptylist {
  color: var(--purple);
  text-align: center;
  font-size: 10rem;
  margin-top: 2rem;
}